module.exports = function (doc, win) {
    var docEl = win.document.documentElement;
    var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize';
    var refreshRem = function () {
        var clientWidth = win.innerWidth || doc.documentElement.clientWidth || doc.body.clientWidth;
        if (!clientWidth) return;
        var fontSize; 
        if(clientWidth >= 1600){
            // fontSize = clientWidth * 16 * 1.27 / 1920;
            fontSize = 20;
        }
        else if(clientWidth >= 768){
            // fontSize = clientWidth * 16 / 1920;
            fontSize = 16;
        }
        else if(clientWidth >= 400){
            fontSize = 14;
        }
        else{
            fontSize = 12;
        }
        // console.log(clientWidth, fontSize);
        docEl.style.fontSize = fontSize + 'px';
    };

    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, refreshRem, false);
    doc.addEventListener('DOMContentLoaded', refreshRem, false);
    refreshRem();
};



