<template>
  <div class="cc-container">
    <div class="cc-header stretch" id="cc_header">
      <div class="hbg">
        <a-row type="flex" justify="center" class="hbar">
          <a-col :xxl="20" :md="22" :span="24">
            <div class="cc-main title">
              <span class="s1">Colon Cancer</span>
              <span class="s2">Dynamic Survival Calculator</span>
            </div>
            <img class="himg" src="./assets/images/himg.png" />
          </a-col>
        </a-row>
      </div>
    </div>

    <a-row class="cc-body" type="flex" justify="center">
      <a-col :xxl="20" :md="22" :span="24">
        <div class="cc-main">
          <a-row v-if="load" :gutter="16">
            <a-col :md="12" :span="24">
              <a-form layout="vertical" class="cc-form">
                <a-form-item class="anm">
                  <slot name="label">
                    <div class="form-lbl">
                      <div class="rect"></div>
                      Survival time since diagnosis
                    </div>
                  </slot>
                  <a-select v-model="survprob">
                    <a-select-option
                      v-for="month in months"
                      :key="month"
                      :value="month"
                    >
                      {{ month }} months
                    </a-select-option>
                  </a-select>
                </a-form-item>
                <a-form-item
                  v-for="(item, index) in point_scores"
                  :key="item.type"
                  class="anm"
                >
                  <slot name="label">
                    <div class="form-lbl">
                      <div class="rect"></div>
                      {{ item.type }}
                    </div>
                  </slot>
                  <a-radio-group v-model="scores[index]">
                    <a-radio-button
                      v-for="point in item.points"
                      :key="point.label"
                      :value="point.value"
                    >
                      {{ point.label }}
                    </a-radio-button>
                  </a-radio-group>
                </a-form-item>
              </a-form>
            </a-col>
            <a-col :md="12" :span="24">
              <div class="cc-result">
                <div class="dline"></div>
                <div class="info-area">
                  <div class="info anm">
                    <div class="circ">1</div>
                    <div class="text">
                      -year survival probability:
                      <span class="res">{{ showProb(1) }}</span>
                    </div>
                  </div>
                  <div class="info anm">
                    <div class="circ">3</div>
                    <div class="text">
                      -year survival probability:
                      <span class="res">{{ showProb(3) }}</span>
                    </div>
                  </div>
                  <div class="info anm">
                    <div class="circ">5</div>
                    <div class="text">
                      -year survival probability:
                      <span class="res">{{ showProb(5) }}</span>
                    </div>
                  </div>
                </div>
                <div class="chart-area">
                  <div class="circ">
                    <a-icon class="label" type="line-chart" />
                  </div>
                  <chart :value="probs"></chart>
                </div>
              </div>
            </a-col>
          </a-row>
          <a-row v-else>
            <a-col>
              <a-result title="Page loading, please wait a moment!">
                <template #icon>
                  <a-icon
                    type="sync"
                    spin
                    :style="{ color: theme.themeColor }"
                  />
                </template>
              </a-result>
            </a-col>
          </a-row>
        </div>
      </a-col>
    </a-row>
    <div class="cc-footer">
      <a-row type="flex" justify="center">
        <a-col :xxl="20" :md="22" :span="24">
          <div class="cc-main">
            <span>Copyright © 2022.Team All rights reserved.</span>
          </div>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import chart from "./components/chart";
export default {
  components: { chart },
  data() {
    return {
      theme: this.$theme.style,
      point_scores: [],
      scores: [],
      survprob: 0,
      cals: [[], [], [], [], []],
      load: false,
      limits: [0, 93, 81, 69, 57, 45],
      mychart: null,
    };
  },
  mounted: function () {
    window.onscroll = function (epic) {
      var hd = document.getElementById("cc_header");
      var scTop = document.documentElement.scrollTop;
      var sh = hd.clientWidth * 0.07;
      if (scTop > sh && !hd.classList.contains("shrink")) {
        hd.classList.remove("stretch");
        hd.classList.add("shrink");
      } else if (scTop <= sh && hd.classList.contains("shrink")) {
        hd.classList.remove("shrink");
        hd.classList.add("stretch");
      }
      var eles = document.getElementsByClassName("anm");
      for (let i = 0; i < eles.length; i++) {
        var ele = eles[i];
        var iv = elementInView(ele);
        if (iv == 0 && !ele.classList.contains("anm_action")) {
          displayScrollElement(ele);
        } else if (iv != 0 && ele.classList.contains("anm_action")) {
          hideScrollElement(ele);
        }
      }
    };
    const elementInView = (el, scrollOffset = 0) => {
      let elementTop = el.getBoundingClientRect().top;
      let eleBottom = elementTop + el.clientHeight;
      if (eleBottom < 0) {
        return -1;
      } else if (
        elementTop >
        (window.innerHeight || document.documentElement.clientHeight) -
          scrollOffset
      )
        return 1;
      else return 0;
    };
    const displayScrollElement = (element) => {
      element.style.visibility = "visible";
      // element.style.opacity = 1;
      element.classList.add("anm_action");
    };
    const hideScrollElement = (element) => {
      element.style.visibility = "hidden";
      // element.style.opacity = 0;
      element.classList.remove("anm_action");
    };
  },
  created: function () {
    var inits = [this.initScores()];
    for (var i = 1; i <= 5; i++) {
      inits.push(this.initCal(i));
    }
    var that = this;
    Promise.all(inits).then(function () {
      var probs = [];
      for (var i = 1; i <= 5; i++) {
        probs.push(that.caculate(i));
      }
      that.probs = probs;
      that.load = true;
    });
  },
  watch: {
    scores: function () {
      if (!this.load) return;
      this.updateProbs();
    },
    survprob: function () {
      if (!this.load) return;
      this.updateProbs();
    },
  },
  methods: {
    initScores: function () {
      var that = this;
      return new Promise(function (resolve, reject) {
        that.$axios.get("./static/data/pointscore.json").then(function (res) {
          that.point_scores = res.data;
          for (var i = 0; i < that.point_scores.length; i++) {
            var p = that.point_scores[i];
            that.scores.push(p.points[0].value);
          }
          resolve();
        });
      });
    },
    initCal: function (y) {
      var that = this;
      return new Promise(function (resolve, reject) {
        that.$axios
          .get("./static/data/" + y + "ycalculator.json")
          .then((res) => {
            that.cals[y] = res.data;
            resolve();
          });
      });
    },
    showProb: function (y) {
      var prob = this.probs[y - 1];
      return prob == -1 ? "" : prob + "%";
    },
    updateProbs: function () {
      let probs = [];
      for (let i = 1; i <= 5; i++) {
        probs.push(this.caculate(i));
      }
      this.probs = probs;
      let resEles = document.getElementsByClassName("res");
      for (let i = 0; i < resEles.length; i++) {
        resEles[i].classList.add("anm_action");
        resEles[i].addEventListener("animationend", () => {
          resEles[i].classList.remove("anm_action");
        });
      }
    },
    caculate: function (y) {
      if (this.survprob > this.limits[y]) {
        return -1;
      }
      var sum = 0;
      for (var i = 0; i < this.scores.length; i++) {
        sum += this.scores[i];
      }
      var survprob = "survprob" + this.survprob;
      var k = 0;
      for (; k < this.cals[y].length; k++) {
        if (this.cals[y][k].points >= sum) break;
      }
      if (
        k > 0 &&
        sum - this.cals[y][k - 1].points < this.cals[y][k].points - sum
      ) {
        k -= 1;
      }
      var prob = this.cals[y][k][survprob];
      prob *= 100;
      prob = Math.round(prob * 100) / 100;
      return prob;
    },
  },
  computed: {
    months: function () {
      var lst = [];
      for (var i = 0; i <= 93; i += 3) {
        lst.push(i);
      }
      return lst;
    },
  },
};
</script>


