<template>
  <div class="chart-container anm">
    <div id="chart"></div>
  </div>
</template>

<script>
export default {
  name: "chart",
  props: ["value"],
  data() {
    return {
      mychart: null,
    };
  },
  mounted: function () {
    var chartDom = document.getElementById("chart");
    this.myChart = this.$echarts.init(chartDom);
    this.option && this.myChart.setOption(this.option);
  },
  watch: {
    value: function () {
      this.option && this.myChart.setOption(this.option);
    },
  },
  computed: {
    option: function () {
      var xs = [];
      var ys = [];
      for (var i = 0; i < this.value.length; i++) {
        if (this.value[i] == -1) continue;
        xs.push(i + 1);
        ys.push(this.value[i]);
      }
      // var fontSize = document.getElementById("chart").clientWidth * 16 /600;
      var docRes = document.getElementsByClassName("cc-result");
      var style;
      if (docRes.length > 0) {
        style = window.getComputedStyle(docRes[0]);
      }
      var fontSize = 16;
      var fontFamily = this.$theme.style.themeFont;
      var fontWeight = 600;
      var fontColor = this.$theme.style.themeColorDark;
      if (style) {
        fontSize = parseInt(style.fontSize);
        fontFamily = style.fontFamily;
        fontWeight = style.fontWeight;
        fontColor = style.color;
      }
      var cw = document.getElementById("chart").clientWidth;
      // console.log(cw);
      if (cw < 310) {
        fontSize = parseInt((cw * 12 * 1.2) / 310);
      }
      return {
        color: this.$theme.style.themeColorBright,
        textStyle: {
          fontSize: fontSize,
          fontFamily: fontFamily,
          fontWeight: fontWeight,
          color: fontColor,
        },
        title: {
          text: "For reference only",
          left: "right",
          bottom: 0,
          textStyle: {
            color: this.$theme.style.themeColorLightFore + "88",
            fontStyle: "italic",
            fontSize: fontSize * 0.9,
            fontWeight: 400,
          },
        },
        grid: {
          //防止标签溢出
          containLabel: true,
          left: fontSize,
          top: fontSize < 14? 42: fontSize * 3,
          bottom: fontSize * 2.5,
        },
        xAxis: {
          type: "category",
          data: xs,
          name: "Year",
          nameTextStyle: {
            align: "center",
            verticalAlign: "top",
            padding: [fontSize * 0.5, fontSize * 0.8, 0, 0],
          },
          axisTick: {
            alignWithLabel: true,
          },
          axisLine: {
            lineStyle: {
              color: this.$theme.style.themeColor,
              width: 3,
              shadowColor: "#84a2ad",
              shadowBlur: 4,
              shadowOffsetY: 2,
              shadowOffsetX: 2,
            },
          },
          axisLabel: {
            color: fontColor,
            fontSize: fontSize
          },
        },
        yAxis: {
          type: "value",
          name: "Survival probability(%)",
          nameTextStyle: {
            padding: [0, 0, fontSize, -(fontSize * 2)],
            // color: this.$theme.style.themeColorDark,
            align: "left",
          },
          min: 0,
          max: 100,
          axisLabel: {
            color: fontColor,
            fontSize: fontSize
          },
        },
        series: [
          {
            data: ys,
            type: "line",
            smooth: true,
            label: {
              show: true,
              color: "inherit",
              fontSize: fontSize * 0.8,
              formatter: function (value) {
                if (value.dataIndex % 2) {
                  return "{lb|" + value.data + "%}";
                } else {
                  return value.data + "%";
                }
              },
              rich: {
                lb: {
                  padding: [0, 0, fontSize<14?-42:-(fontSize * 3), 0],
                  fontSize: fontSize * 0.8,
                },
              },
              textShadowColor: "#f0ccc2",
              textShadowBlur: 3,
              textShadowOffsetY: 2,
              textShadowOffsetX: 2,
            },
            symbolSize: 8,
            lineStyle: {
              width: 5,
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: this.$theme.style.themeColorBrightL, // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: this.$theme.style.themeColorBrightR, // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
          },
          {
            type: "line",
            color: this.$theme.style.themeColorLight,
            markArea: {
              data: [[{ yAxis: "20" }, { yAxis: "40" }]],
              silent: true,
            },
          },
          {
            type: "line",
            color: this.$theme.style.themeColorLight,
            markArea: {
              data: [[{ yAxis: "60" }, { yAxis: "80" }]],
              silent: true,
            },
          },
        ],
      };
    },
  },
  methods: {
    hexToHsl: function (hex) {},
  },
};
</script>
<style scoped>
.chart-container {
  position: relative;
  width: 100%;
  padding-bottom: 80%;

  background-color: rgba(255, 255, 255, 0.35);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: rgba(122, 135, 145, 0.19) 0px 6px 15px 0px;
  -webkit-box-shadow: rgba(122, 135, 145, 0.19) 0px 6px 15px 0px;

  border-radius: 4px;
  -webkit-border-radius: 4px;
  color: rgb(255, 255, 255);
}
#chart {
  width: 90%;
  /* min-width: 280px; */
  height: 92%;
  position: absolute;
  left: 4%;
  top: 5%;
}

</style>


